body {
  margin: 0;
  padding: 0;
  font-family: "Segoe UI", sans-serif;
  background: linear-gradient(135deg, #f8f9fa, #e0eaff);
  color: #333;
}

.app {
  padding: 20px;
  text-align: center;
}

h1 {
  font-size: 32px;
  color: #007bff;
  margin-bottom: 10px;
}

.scoreboard {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 16px;
  margin-bottom: 20px;
  align-items: center;
}

.scoreboard p {
  font-weight: 500;
  font-size: 16px;
}

.scoreboard button {
  padding: 10px 18px;
  font-weight: 600;
  background-color: #007bff;
  border: none;
  color: white;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.scoreboard button:hover {
  background-color: #0056b3;
}

.grid {
  display: grid;
  grid-template-columns: repeat(4, 100px);
  gap: 20px;
  justify-content: center;
  margin-bottom: 30px;
}

.win-message {
  background-color: #d4edda;
  color: #155724;
  padding: 16px 24px;
  border-radius: 12px;
  display: inline-block;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
  font-weight: bold;
}
