.card {
  width: 100px;
  height: 100px;
  perspective: 800px;
  cursor: pointer;
}

.inner {
  position: relative;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: transform 0.6s;
}

.inner.flipped {
  transform: rotateY(180deg);
}

.front,
.back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
  display: flex;
  align-items: center;
  justify-content: center;
}

.front {
  background-color: #007bff;
  color: white;
  font-size: 28px;
}

.back {
  transform: rotateY(180deg);
  background: white;
  overflow: hidden;
}

.back img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
}
